:host {
  display: block;
  cursor: pointer;
  min-height: 75px;
}
.sc-dc-council-dropzone {
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(160px,1fr));
  grid-auto-flow: column;
  grid-auto-columns: minmax(160px,1fr);
  overflow-x: auto;
}

#title {
  margin: 0;
  font-size: 1.1rem;
  font-weight: 400;
}