:host {
  display: block;
  min-height: 75px;
}
/* TODO: Remove need to style a hidden sortable class */
.sc-dc-council-dropzone {
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(160px,1fr));
  grid-auto-flow: column;
  grid-auto-columns: minmax(160px,1fr);
  overflow-x: auto;
  grid-gap: 12px;

}

#title {
  margin: 0;
  font-size: 1.1rem;
  font-weight: 400;
}